.service-search{
    width: 95%;
    margin: auto;
}

.card-title{
    font-size: 16px;
    margin-bottom: 10px;
}

.card-description{
    font-size: 12px;
    height: 80px;
    color: #6F6F6F;
    font-style: italic;
}

.card{
    border: 1px solid #CACACA;
}

.services-container {
    width: 95%;
    margin: auto;
}

.services-container ul{
    padding: 0px;
    margin: 0px;
    display: flex;
    flex-wrap: wrap ;
    column-gap: 10px;
    row-gap: 10px;
    list-style: none;
}

.services-container ul li{
    width: 250px;
    padding: 10px;
}

.disabled-card {
    pointer-events: none; /* Blocks mouse events */
    opacity: 0.5;
    cursor: not-allowed;
  }
  
  .disabled-content {
    text-decoration: none;
    color: inherit;
    display: block;
    height: 100%;
  }