.main{
    margin-left: 250px;
    width: 80%;
    height: 100vh;
    background-color: white;
}

.top-bar{
    border-bottom: 1px solid #E3E3E3;
    padding-left: 30px;
}

.topbar{
    display: flex;
    align-items: center;
    height: 45px;
}

.topbar-text{
    font-weight: 400;
}

.topbar ul{
    margin: 0px;
}

.prime-button button{
    font-size: 16px;
    padding: 0px 15px;
    background-color: #E43737;
    border: none;
    color: white;
    font-family: "Poppins", sans-serif;
    align-items: center;
    height: 40px;
    display: flex;
    align-items: center;
    font-family: "Poppins", sans-serif;
    cursor: pointer;
}

.prime-button button:hover{
    background-color: #c92e2e;
}

.secondary-button button{
    font-size: 16px;
    padding: 0px 15px;
    background-color: white;
    border: 1px solid #CBCBCB;
    color: black;
    font-family: "Poppins", sans-serif;
    align-items: center;
    height: 40px;
    display: flex;
    align-items: center;
    cursor: pointer;
}

.btn-icon{
    margin-right: 10px;
}

@media (max-width: 1920px) {
    .main {
        margin-left: 250px;
        width: calc(100% - 250px);
    }
}

.options{
    width: 205px;
    border: 1px solid #CBCBCB;
    text-align: center;
}

.options select{
    border: none;
    outline: none;
    background-color: transparent;
    font-size: 16px;
    width: 90%;
    height: 40px;
    font-family: "Poppins", sans-serif;
}

.menu-options{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 95%;
    margin: auto;
    padding: 20px 0px;
}

.all-btns{
    display: flex;
}

.options_{
    display: flex;
    align-items: center;
}

.margin-left{
    margin-left: 10px;
}

.label{
    font-size: 14px;
    color: gray;
    font-weight: 400;
}

.account{
    color: black;
    font-size: 14px;
    font-weight: 600;
}

.analysis{
    display: flex;
    width: 95%;
    margin: auto;
    height: 267px;
}

.instance-status{
    flex: 1;
}
.status-dis{
    flex: 2;
}
.instance-types{
    flex: 3;
}

.instance-main-container{
    border: 1px solid #D9D9D9;
    text-align: center;
    display: flex;
    justify-content: center;
    height: 74px;
    margin: 10px 0px;
}

.instance-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 90%;
    align-items: center;
    text-align: left;
}

.count{
    font-size: 24px;
}

.status-dis{
    border: 1px solid #CBCBCB;
    margin: 10px;
    margin-right: 0px;
}

.instance-types{
    border: 1px solid #CBCBCB;
    margin: 10px;
    margin-right: 0px;
}

.chart-title {
    position: absolute;
    color: black;
    margin-left: 20px;
    margin-top: 10px;
    font-weight: 300;
    margin-bottom: 10px;
    font-size: 14px;
}

.instances-title-container{
    border-bottom: 1px solid #D9D9D9;
    height: 40px;
    display: flex;
    align-items: center;
    width: 95%;
    margin: auto;
}

.instances{
    margin: auto;
    width: 95%;
}
.instances-container {
    border-left: 1px solid #D9D9D9;
    border-right: 1px solid #D9D9D9;
    border-top: 1px solid #D9D9D9;
    height: 100%;
    box-sizing: border-box;
}

.table-headings{
    display: flex;
    width: 95%;
    margin: auto;
    color: #4b4b4b;
    font-size: 12px;
    padding: 10px 0px;
    justify-content: center;
}

.head1, .head3{
    flex: 2;
}

.head2, .head4, .head5, .head6, .head7{
    flex: 1;
}


.table-data{
    display: flex;
    width: 95%;
    margin: auto;
    color: #4b4b4b;
    font-size: 14px;
    padding: 10px 0px;
    justify-content: center;
    border-bottom: 1px solid #CBCBCB;
}


/* Slide-in Configuration Panel */
.config-panel {
    position: fixed;
    top: 0;
    right: 0;
    width: 350px;
    height: 100vh;
    background-color: white;
    border-right: 1px solid #ddd;
    box-shadow: 2px 0px 40px 0px #00000023;
    z-index: 9999;
    padding: 20px;
    box-sizing: border-box;
  }
  
  .config-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #eee;
    padding-bottom: 10px;
  }
  
  .config-header h3 {
    margin: 0;
    font-size: 18px;
    font-weight: 600;
  }
  
  .close-config {
    background: transparent;
    border: none;
    font-size: 20px;
    cursor: pointer;
    color: #888;
  }
  
  .config-body {
    margin-top: 20px;
    font-family: "Poppins", sans-serif;
    font-size: 14px;
    color: #333;
  }

.input-prime{
    width: 100%;
    border: 1px solid #CBCBCB;
    text-align: center;
    margin: 10px 0px;
  }

.apps{
    width: 100px;
}

.loading-overlay {
    position: fixed;
    top: 0;
    left: 250px;
    width: calc(100% - 250px);
    height: 100%;
    background-color: rgba(255, 255, 255, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999;
  }

  .link{
    text-decoration: none;
    color: black;
  }

.input-prime input{
    background-color: transparent;
    font-family: "Poppins", sans-serif;
    outline: none;
    border: none;
    height: 40px;
    width: 90%;
}
.input-width{
    width: 300px;
}

.head2{
    margin: 0px 10px;
}


.state{
    background-color: green;
    text-align: center;
    margin: 0px 10px;
    border-radius: 10px;
    color: white;
    font-weight: 500;
    display: flex;
    height: 30px;
    align-items: center;
    justify-content: center;
}

.state-stop{
    background-color: #E43737;
    text-align: center;
    margin: 0px 10px;
    border-radius: 10px;
    color: white;
    font-weight: 500;
    display: flex;
    height: 30px;
    align-items: center;
    justify-content: center;
}

.margin-right{
    margin-right: 10px;
}

.menu-items_ ul li{
    margin-bottom: 15px;
}