body{
    font-family: "Poppins", sans-serif;
}

.projects{
    display: flex;
}

.navbar-container{
    padding-top: 30px;
}

.project-icon {
    width: 30px;
    height: 30px;
    background-color: #D9C0A3;
    display: flex;
    justify-content: center;
    align-items: center;     
    text-align: center;
    color: #000000;
    font-size: 16px;
}

.search-bar{
    display: flex;
    margin-top: 15px;
    width: 195px;
    border: 1px solid #E0E0E0;
    align-items: center;
    text-align: center;
}

.search-bar input{
    outline: none;
    border: none;
    font-size: 14px;
    height: 40px;
    width: 80%;
    margin-left: 5px;
    font-family: "Poppins", sans-serif;
}


.search-icon{
    margin-left: 10px;
    font-size: 20px;
    color: #6F6F6F;
}

.projects{
    align-items: center;
    margin-top: 15px;
    
}

.project-name{
    margin-left: 10px;
    font-size: 16px;
    font-weight: 600;
}

.nav-bar{
    position: fixed;
    width: 250px;
    background-color: white;
    height: 100vh;
    box-shadow: 2px 0px 40px 0px #00000023;
}
.navbar-container{
    width: 80%;
    margin: auto;
}

.menu{
    padding: 10px 0px;
    border-bottom: 1px solid #CACACA;
}

.menu-items ul{
    padding: 0px;
    margin: 0px;
    margin-top: 5px;
}

.menu-text{
    font-size: 14px;
    margin-left: 10px;
}

.menu-items ul li{
    list-style: none;
    display: flex;
    align-items: center;
    color: rgb(47, 47, 47);
}

.menu-heading{
    color: #A9A9A9;
    margin-top: 10px;
    font-size: 12px;
}

.icons{
    font-size: 20px;
}

.secondery-menu{
    border: none;
}