.smartscale{
    padding-top: 20px;
    width: 95%;
    margin: auto;
}

.smartscale-container{
    display: flex;
}

.smartscale-dashboard{
    flex: 10;
}

.suggest{
    flex: 3;
}

.suggestion-cards ul{
    padding: 0px;
    margin: 0px;
    list-style: none;
}

.card-top{
    display: flex;
    align-items: center;
}

.card-heading{
    font-size: 16px;
    font-weight: 600;
}
.score{
    font-size: 12px;
    padding: 10px;

}
.sug{
    font-size: 12px;
    color: #6F6F6F;
    font-style: italic;
}

.suggestion-cards ul li{
    border-left: 4px solid #E43737;
    padding: 5px 10px;
    margin-top: 15px;
}

.smartscale-dashboard {
    background: #fff;
    border-radius: 10px;
    padding-right: 50px;
    /* margin-bottom: 24px; */
    /* box-shadow: 0 2px 8px rgba(0,0,0,0.1); */
  }

.margin-down{
    margin-bottom: 15px;
}